import React from "react";

const TimeTable = ({ timetable, breakTimings }) => {
  return (
    <>
     <h2 className="text-2xl font-medium mb-2">Time Table </h2>
     <div className="overflow-x-auto">
      <table className="table-auto w-full text-left bg-gray-50 border border-gray-200 rounded-lg shadow-md">
        <thead className="bg-gray-200">
          <tr className="text-xs font-medium uppercase tracking-wide text-gray-700">
            <th className="px-6 py-4">Time</th>
            <th className="px-6 py-4">Monday</th>
            <th className="px-6 py-4">Tuesday</th>
            <th className="px-6 py-4">Wednesday</th>
            <th className="px-6 py-4">Thursday</th>
            <th className="px-6 py-4">Friday</th>
          </tr>
        </thead>
        <tbody>
          {timetable.map((slot, index) => (
            <tr key={index} className={`text-gray-700 ${breakTimings.includes(slot.time) ? 'bg-yellow-100' : 'bg-green-100'}`}>
              <td className="border px-6 py-4">{slot.time}</td>
              <td className="border px-6 py-4">{slot.Monday}</td>
              <td className="border px-6 py-4">{slot.Tuesday}</td>
              <td className="border px-6 py-4">{slot.Wednesday}</td>
              <td className="border px-6 py-4">{slot.Thursday}</td>
              <td className="border px-6 py-4">{slot.Friday}</td>
            </tr>
          ))}
        </tbody>
      </table>
     </div>
    </>
  );
};

export default TimeTable;
