import React from 'react';
import anime from 'animejs';
import { useEffect } from 'react';


const StudentDetail = ({ student }) => {

  useEffect(() => {
    anime({
      targets: '#main-bg',
      backgroundColor: ['#FFFFFF', '#FFFFF8'],
      duration: 6000,
      easing: 'easeInOutQuad',
      direction: 'alternate',
      loop: true
    });
  }, []);

  return (

    <>
      <div className="flex flex-col items-center p-10 ">

        <img
          src={'https://picsum.photos/200'}
          alt={student.name}
          className="w-64 h-64 rounded-full object-cover"
        />
        <div className="mt-5 text-center">
          <p className="text-2xl font-bold text-gray-800">{student.name}</p>
          <p className="text-sm text-gray-600">{student.address}</p>
        </div>

      </div>
      <div className="flex flex-col items-center p-10 bg-white rounded-lg shadow-md" id='main-bg'>



        <div className="mt-10 flex ">
          <div className="p-10 bg-grey-200 rounded-lg shadow-md flex-1 text-center mr-4">
            <p className="text-lg font-bold text-gray-800">Student Information</p>
            <p className="text-sm text-gray-600 mb-2">Class: {student.class}</p>
            <p className="text-sm text-gray-600 mb-2">Section: {student.section}</p>
            <p className="text-sm text-gray-600">Roll No.: {student.rollNumber}</p>
          </div>
          <div className="p-10 bg-grey-200 rounded-lg shadow-md flex-1 text-center ml-4 mr-4">
            <p className="text-lg font-bold text-gray-800">Parent Information</p>
            <p className="text-sm text-gray-600 mb-2">Father's Name: {student.fatherName}</p>
            <p className="text-sm text-gray-600 mb-2">Mother's Name: {student.motherName}</p>
            <p className="text-sm text-gray-600">Guardian's Name: {student.guardianName}</p>
          </div>
          <div className="p-10 bg-grey-200 rounded-lg shadow-md flex-1 text-center ml-4">
            <p className="text-lg font-bold text-gray-800">Contact Information</p>
            <p className="text-sm text-gray-600 mb-2">Phone Number: {student.phoneNumber}</p>
            <p className="text-sm text-gray-600 mb-2">Email: {student.email}</p>
            <p className="text-sm text-gray-600">Zip Code: {student.zipCode}</p>
          </div>
        </div>
        <div className="mt-10 flex ">

        </div>

      </div>
    </>


  )

};

export default StudentDetail;