import React, { Component } from 'react';
import { Calendar, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import "./calender.css"
import 'react-big-calendar/lib/css/react-big-calendar.css';

const localizer = momentLocalizer(moment);

class StudentAttendanceCard extends Component {
  state = {
    selectedMonth: null,
  };

  handleMonthSelection = (month) => {
    this.setState({ selectedMonth: month });
  };

  months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];

  filteredData = () => {
    const { selectedMonth } = this.state;
    const { sampleData } = this.props;

    return selectedMonth !== null
      ? sampleData.filter((result) => result.date.includes(selectedMonth))
      : sampleData;
  };

  events = () => {
    const filteredData = this.filteredData();
  
    return filteredData.map((result) => ({
      start: new Date(result.date),
      end: new Date(result.date),
      title: result.attendance,
      allDay: true,
      style: {
        backgroundColor: result.attendance === 'present' ? 'green' : result.attendance === 'absent' ? 'red' : 'grey',
      },
    }));
  };

  render() {
    const { selectedMonth } = this.state;
    const { events } = this;
    const { months } = this;

    return (
      <div className="p-8 bg-white rounded shadow">
        <h2 className="text-2xl font-medium">Student Attendance</h2>
        <div className="mt-4">
          <Calendar
            localizer={localizer}
            events={events()}
            startAccessor="start"
            endAccessor="end"
            eventPropGetter={(event) => {
              return {
                style: {
                  backgroundColor: event.style.backgroundColor,
                },
              };
            }}
            style={{ height: 500 }}
            className="calendar"
          />
        </div>
      </div>
    );
  }
}

export default StudentAttendanceCard;