import React, { useState } from "react";
import { Table, Chart } from "./TandCforRes";
import "./detailResult.css";

const StudentResultCard = ({ studentData }) => {
  const [selectedMonth, setSelectedMonth] = useState("");

  const handleMonthSelection = (month) => {
    setSelectedMonth(month);
  };

  const filteredData = selectedMonth !== ""
    ? studentData.filter((result) => result.month === selectedMonth)
    : studentData;

  const groupByMonth = filteredData.reduce((result, currentValue) => {
    (result[currentValue.month] = result[currentValue.month] || []).push(currentValue);
    return result;
  }, {});

  return (
    <div className="p-8 bg-white rounded shadow">
      <h2 className="text-2xl font-medium">Student Results</h2>
      <div className="mt-4">
        <select
          value={selectedMonth}
          onChange={e => handleMonthSelection(e.target.value)}
          className="py-2 px-4 bg-white border border-gray-300 rounded-lg text-sm font-medium"
        >
          <option value="">All</option>
          {Array.from(new Set(studentData.map(result => result.month))).map(month => (
            <option key={month} value={month}>
              {month}
            </option>
          ))}
        </select>
      </div>
      <div className="flex-container ">
        <table className="w-full table-auto text-left">
          <thead>
            <tr className="bg-gray-100">
              <th className="p-2 font-medium">Month</th>
              <th className="p-2 font-medium">Subject</th>
              <th className="p-2 font-medium">Score</th>
            </tr>
          </thead>
          <tbody>
            {Object.entries(groupByMonth).map(([month, subjects]) => (
              <React.Fragment key={month}>
                <tr className="border-t border-gray-300 text-xl font-medium">
                  <td className="p-2 bg-yellow-200" colSpan="3">{month}</td>
                </tr>
                {subjects.map(subject => (
                  <tr key={subject.student} className="border-t border-gray-300">
                    <td className="p-2"></td>
                    <td className="p-2 text-sm font-medium">{subject.subject}</td>
                    <td className="p-2 text-sm font-medium">{subject.score}</td>
                  </tr>
                ))}
              </React.Fragment>
            ))}
          </tbody>
        </table>
        <Chart data={filteredData} />
      </div>
    </div>
  );
};

export default StudentResultCard;
