import { lazy } from "react";
import { numberOfStudents } from "../../../data/DataParent";
const Parent = lazy(()=>import('../Parent'));
const Leads  = lazy(()=>import('../Leads'));


const routes = [
    {
        path: '*', // the url
        component: numberOfStudents===1?Parent:Leads, // view rendered
      },

      {
        path: '/leads/:id',
        component: Parent,
      }
    ]

export default routes;