import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Filler,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import TitleCard from '../../../../common/component/Cards/TitleCard';
import Modal from "react-modal";
import { useState } from 'react';
import StudentResultCard from '../../../student/componets/dashboard/DetailResult';




const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    width: '700px',
    height: '600px',
    backgroundColor:"#fff",
    boxShadow: '0px 10px 20px rgba(0, 0, 0, 0.1)',
    borderRadius: '10px'
  }
};


ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Filler,
  Legend
);

Modal.setAppElement('#root');

function LineChart({studentData,title,labelName,backgroundColor , borderColor}) {

  let subtitle;
  const [modalIsOpen, setIsOpen] = useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = '#f00';
  }

  function closeModal() {
    setIsOpen(false);
  }


  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
    },
  };


  const labels = ['January', 'February', 'March', 'April', 'May', 'June', 'July', "August", "September", "October", "November", "December"];

  const data = {
    labels,
    datasets: [
      {
        fill: true,
        label: labelName,
        data: labels.map(() => { return Math.random() * 100 }),  // data sections 
        borderColor: borderColor,
        backgroundColor: backgroundColor,
      },
    ],
  };


  // console.log(data)

  return (
    <>
    <TitleCard title={title} id="lineModal">
      <Line data={data} options={options} />
      {studentData===null?console.log(" "):<button className="bg-white hover:bg-gray-100 text-gray-800 font-semibold py-2 px-4 border border-gray-400 rounded shadow" onClick={openModal}>
        Details
      </button>}
    </TitleCard>
    <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
       <StudentResultCard studentData={studentData}/>
      </Modal>
    </>
  )
}


export default LineChart