function Leads() {


    let subtitle;
    const [modalIsOpen, setIsOpen] = useState(false);

    function openModal() {
        setIsOpen(true);
    }

    function afterOpenModal() {
        // references are now sync'd and can be accessed.
        subtitle.style.color = '#f00';
    }

    function closeModal() {
        setIsOpen(false);
    }


    return (
        <>

            <TitleCard title="Student List" topMargin="mt-2"  >

                {/* Leads List in table format loaded from slice after api call */}
                <div className="overflow-x-auto w-full ">
                    <table className="table w-full card w-full p-6 bg-base-100 shadow-xl">
                        <thead>
                            <tr>
                                <th className="bg-gray-200 text-slate-600">Student Id</th>
                                <th className="bg-gray-200 text-slate-600">Name</th>
                                <th className="bg-gray-200 text-slate-600">Performance</th>
                                <th className="bg-gray-200 text-slate-600">Attendece</th>
                                <th className="bg-gray-200 text-slate-600">Fees</th>
                                <th className="bg-gray-200 text-slate-600">Detailed View</th>
                                <th className="bg-gray-200 text-slate-600"></th>
                            </tr>
                        </thead>
                        <tbody className="mx-4 my-4">
                            {studentdetailsData.map((student, index) => (
                                <tr key={index}>
                                    <td className="bg-white-200 ">{student.id}</td>
                                    <td className="bg-white-200">{student.name}</td>
                                    <td className="bg-white-200 ">
                                        <button className={`m-2 px-8 rounded-md bg-white-100 text-xl ${student.performance < 40 ? 'bg-red-200' : student.performance > 80 ? 'bg-green-200' : 'bg-blue-200'}`} >
                                            {student.performance}%
                                        </button></td>
                                    <td className="bg-white-200 " >
                                        <button className={`m-2 px-4 rounded-md bg-white-100 text-xl ${student.attendance < 40 ? 'bg-red-200' : student.attendance > 80 ? 'bg-green-200' : 'bg-blue-200'}`} >
                                            {student.attendance}%
                                        </button></td>
                                    <td className="bg-white-200 ">
                                        <button className={`m-2 px-4 rounded-md bg-white-100 text-xl ${student.fees > 0 ? 'bg-red-200 animate-pulse' : 'bg-green-200'}`} onClick={openModal}>
                                            {student.fees}
                                        </button>
                                    </td>
                                    <td className="bg-white-200">
                                        <button className="border-2 border-blue-800 text-black hover:bg-blue-700 hover:text-white ml-10 py-2 px-4 rounded" >
                                            <Link to={`/leads/${student.id}`}>View</Link>
                                        </button>
                                    </td>
                                    <td className="bg-white-200"></td>
                                </tr>
                            ))}

                        </tbody>
                    </table>
                </div>
            </TitleCard>

            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <FeeStatsTable />
            </Modal>
        </>
    )
}


export default Leads