import React from "react";
import { Chart } from "chart.js";

const Table = ({ data }) => {
  return (
    <table className="table-auto m-4">
      <thead className="bg-gray-800 text-white">
        <tr>
          <th className="px-4 py-2">Month</th>
          <th className="px-4 py-2">Subject</th>
          <th className="px-4 py-2">Score</th>
        </tr>
      </thead>
      <tbody>
        {data.map((result, index) => (
          <tr key={index} className="border-t border-gray-600">
            <td className="px-4 py-2">{result.month}</td>
            <td className="px-4 py-2">{result.subject}</td>
            <td className="px-4 py-2">{result.score}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};

const ResultsChart = ({ data }) => {
  const canvasRef = React.useRef(null);

  // Calculate the average score for each subject
  const subjects = Array.from(new Set(data.map((result) => result.subject)));
  const subjectAverages = subjects.map((subject) => {
    const subjectData = data.filter((result) => result.subject === subject);
    const average =
      subjectData.reduce((total, result) => total + result.score, 0) /
      subjectData.length;
    return { subject, average };
  });

  React.useEffect(() => {
    const ctx = canvasRef.current.getContext("2d");
    const chart = new Chart(ctx, {
      type: "bar",
      data: {
        labels: subjects,
        datasets: [
          {
            label: "Average Score",
            data: subjectAverages.map((subjectAverage) => subjectAverage.average),
            backgroundColor: "rgba(54, 162, 235, 0.5)",
            borderColor: "rgb(54, 162, 235)",
            borderWidth: 1,
          },
        ],
      },
      options: {
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
              },
            },
          ],
        },
      },
    });
    return () => {
      chart.destroy();
    };
  }, [subjectAverages]);

  return (
    <div className="m-4">
      <h3 className="text-lg font-medium">Average Scores by Subject</h3>
      <canvas ref={canvasRef} />
    </div>
  );
};






export { Table, ResultsChart as Chart };