import Modal from "react-modal";
import React , {useState , useEffect} from "react"
import NoticeBoard from "./NoticeBoard";
import TimeTable from "./Timetable";
import "./dashboardstats.css";
import {numberOfStudents } from "../../../../data/DataParent"

var counter = 0;
var studentNoticeVisit = [];
for(let i=0;i<numberOfStudents;i++) studentNoticeVisit.push(0);


function DashboardStats({ gen_id,blink_id, title, icon, value, description, colorIndex ,notices , timetableData,breakTimingsData , studentId }) {
  
 console.log(studentNoticeVisit)

  const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
      width: '700px',
      height: '400px',
      backgroundColor: '#fff',
      boxShadow: '0px 10px 20px rgba(0, 0, 0, 0.1)',
      borderRadius: '10px'
    }
  };
  
  const COLORS = ["primary", "primary"];

  const [blink, setBlink] = useState(false);
  const [modalIsOpen, setIsOpen] = useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
    counter++;
    setBlink(false);
    studentNoticeVisit[studentId]=1;
  }

  useEffect(() => {
    let intervalId = null;
    if (blink_id === 1 && !modalIsOpen && counter===0) {
      intervalId = setInterval(() => {
        setBlink((prevBlink) => !prevBlink);
      }, 1000);
    }
    return () => {
      if (intervalId) clearInterval(intervalId);
    };
  }, [modalIsOpen, blink_id]);



  return (
    <>
      <div
        className={"stats " }
        onClick={blink_id === 1 || gen_id === 1? openModal : console.log(" ")}
      >
        <div className="stat">
          <div className={`stat-figure text-${COLORS[colorIndex % 2]}`}>
            {icon}
          </div>
          <div className="stat-title">{title}</div>
          <div className={`stat-value text-${COLORS[colorIndex % 2]}`}>
            {value}
          </div>
          <div>{description}</div>
        </div>
      </div>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
      >
        {gen_id===1?<TimeTable timetable={timetableData} breakTimings={breakTimingsData} />
:<NoticeBoard notices={notices}/>}
      </Modal>
    </>
  );
}

export default DashboardStats;

