import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
import TitleCard from '../../../../common/component/Cards/TitleCard';
import { data_attendence_absent, data_attendence_present } from '../../../../data/DataParent';
import Modal from "react-modal";
import { useState } from 'react';

import StudentAttendenceCard from '../../../student/componets/dashboard/Calender';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    width: '600px',
    height: '680px',
    backgroundColor: '#fff',
    boxShadow: '0px 10px 20px rgba(0, 0, 0, 0.1)',
    borderRadius: '10px'
  }
};

function BarChart({sampleAttendence}) {

  let subtitle;
  const [modalIsOpen, setIsOpen] = useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = '#f00';
  }

  function closeModal() {
    setIsOpen(false);
  }

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      }
    },
  };

  const labels = ['January', 'February', 'March', 'April', 'May', 'June', 'July', "August", "September", "October", "November", "December"];

  const data = {
    labels,
    datasets: [
      {
        label: 'Absent',
        data: data_attendence_absent,
        backgroundColor: 'rgba(255, 99, 132, 1)',
      },
      {
        label: 'Present',
        data: data_attendence_present,
        backgroundColor: 'rgba(53, 235, 162, 1)',
      },
    ],
  };

  return (
    <>
      <TitleCard title={"Attendence "}>
        <Bar options={options} data={data} />
        <button className="bg-white hover:bg-gray-100 text-gray-800 font-semibold mt-10 py-2 px-4 border border-gray-400 rounded shadow" onClick={openModal}>
          Details
        </button>
      </TitleCard>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <StudentAttendenceCard sampleData={sampleAttendence}/>
      </Modal>

    </>
  )
}


export default BarChart